.NavigationItems {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    align-items: center;
}


.NavigationItems li {
    margin: 10px 0;
    box-sizing: border-box;
    display: block;
    width: 100%;
}

.NavigationItems li a {
    color: #8f5c2c;
    text-decoration: none;
    width: 100%;
    box-sizing: border-box;
    display: block;
}

.NavigationItems li a:hover,
.NavigationItems li a:active,
.NavigationItems li a.active {
    color: #40a4c8 !important;
}

@media (min-width: 500px) {

    .NavigationItems {
        flex-direction: row;
    }
    
    .NavigationItems li {
        margin: 0;
        display: flex;
        height: 100%;
        width: auto;
        align-items: center;
    }
    
    .NavigationItems li a {
        color: white;
        height: 100%;
        padding: 16px 10px;
        border-bottom: 4px solid transparent;
    }
    
    .NavigationItems li a:hover,
    .NavigationItems li a:active,
    .NavigationItems li a.active {
        color: white;
        background-color: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
    }
}




